import './App.css';
import { Container, Typography, Button, TextField, Checkbox, FormControlLabel, FormControl, FormHelperText, Radio, FormLabel, RadioGroup } from '@material-ui/core';
import { makeStyles } from '@material-ui/core/styles';
import { useState, useRef } from 'react';
import {BrowserRouter as Router, Route} from 'react-router-dom';
import React from 'react';
import { Link, useHistory } from 'react-router-dom';


const useStyles = makeStyles({
  tekstikentta : {
                    marginBottom : "10px",
                    marginTop : "10px"
                 }              
});

function App() {

  const tyylit = useStyles();
  const history = useHistory();
  const lomaketiedot = useRef({});
  const [virheilmoitus, setVirheilmoitus] = useState({});
  function getRandomInt(max) {
    return Math.floor(Math.random() * max);
  }
  const lomakeKasittelija = (e) => {

    e.preventDefault();

    let virheet = {};

    if (!lomaketiedot.current.summa) {

      virheet = {...virheet, summa : "Et voi pelata ilman panosta"}
      
    } else{
      if (isNaN(lomaketiedot.current.summa)) {
        virheet = {...virheet, summa : "Summa tulee sytt numerona!"}
      } else{
        if (lomaketiedot.current.summa < 1){
          virheet = {...virheet, summa : "Vhimmissumma: 1 euro!"}
        }
      }
    }

    if (!lomaketiedot.current.luku) {

      virheet = {...virheet, luku : "Muista veikata!"}
      
    } else {

      if (isNaN(lomaketiedot.current.luku)) {
        virheet = {...virheet, luku : "Luvun tytyy olla numero!"}
      }else{
        if (lomaketiedot.current.luku < 1){
          virheet = {...virheet, luku : "Luvun tulee olla vlill 1-100!"}
        }
        if (lomaketiedot.current.luku > 100){
          virheet = {...virheet, luku : "Luvun tulee olla vlill 1-100!"}
        }
      }
      
    }

    if (!lomaketiedot.current.ehdot) {

      virheet = {...virheet, ehdot : "Sinun tytyy hyvksy kyttehdot"}
      
    } 

    

    if (Object.entries(virheet).length > 0) {

      setVirheilmoitus({...virheet});

    } else {

      setVirheilmoitus({});
      
      

    }
    
    

  }

  const syoteKasittelija = (e) => {

    let arvo = (e.target.type === "checkbox") ? e.target.checked : e.target.value;

    lomaketiedot.current[e.target.name] = arvo;

  }
  const tester = () => {
    let vastaus = getRandomInt(20);
      let voitto = lomaketiedot.current.summa * 2;
      let tasa = lomaketiedot.current.summa;
      let jatti = lomaketiedot.current.summa * 10;
    
      if (vastaus < 5){
        return "Voitit pelisummasi takaisin! ";
      }else if (vastaus > 4 && vastaus < 12){
        return "VOITTO! Voitit " + voitto + " euroa!";
      }else if (vastaus > 11 && vastaus < 19){
        return "Hvisit pelin! Menetit " + tasa + " euroa.";
      }else if (vastaus === 19){
        return "JTTIVOITTO! Voitit jopa " + jatti + " euroa! Onnittelut!";
        
      }
    
  }

    

  return (
    <Container maxWidth="sm">
      <br></br>
      <Typography variant="h5">Lottoarvonta</Typography>
      <br></br>
      <Typography variant="h6">Onnea peliin!</Typography>

      <form onSubmit={lomakeKasittelija}>

      <TextField 
        name="summa"
        variant="outlined"
        label="Kuinka paljon haluat lyd vetoa?"
        fullWidth={true}
        className={tyylit.tekstikentta}
        error={Boolean(virheilmoitus.summa)}
        helperText={virheilmoitus.summa}
        onChange={ syoteKasittelija }
      />

      <TextField 
        name="luku"
        variant="outlined"
        label="Onnenluku 1-100:"
        fullWidth={true}
        className={tyylit.tekstikentta}
        error={Boolean(virheilmoitus.luku)}
        helperText={virheilmoitus.luku}        
        onChange={ syoteKasittelija }
      />

      <FormControl>
        <FormControlLabel 
          control={<Checkbox 
                      name="ehdot" 
                      onChange={ syoteKasittelija }/>}
          label="Hyvksyn snnt"
        />
        <FormHelperText 
          error={Boolean(virheilmoitus.ehdot)}
        >{virheilmoitus.ehdot}</FormHelperText>
      </FormControl>
      
      <Button 
        type="submit"
        variant="contained" 
        color="primary"
        fullWidth={true}
        size="large"
      >
        Pelaa
      </Button><br></br><br></br><br></br>
      {tester()}

      </form>
      
    </Container>
  );
}

export default App;